'use client';
import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';

import ProductList from '../components/ProductList';
import Cart from '../components/Cart';
import OrderSummary from '../components/OrderSummary';
import OrderList from '../components/OrderList';

const API_PRODUCTS_URL = 'http://oms-backend.test/api/products';
const API_ORDERS_URL = 'http://oms-backend.test/api/orders';

export default function Home() {
  const [products, setProducts] = useState([]);
  const [cart, setCart] = useState([]);
  const [order, setOrder] = useState(null);
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [orderStatus, setOrderStatus] = useState(null); // ‚úÖ lifted state

  const router = useRouter();

  // Fetch initial data
  useEffect(() => {
    const fetchInitialData = async () => {
      try {
        setLoading(true);
        const [prodRes, orderRes] = await Promise.all([
          fetch(API_PRODUCTS_URL),
          fetch(API_ORDERS_URL),
        ]);

        if (!prodRes.ok) throw new Error('Failed to fetch products');
        if (!orderRes.ok) throw new Error('Failed to fetch orders');

        const prodData = await prodRes.json();
        const orderData = await orderRes.json();

        setProducts(prodData);
        setOrders(orderData);
      } catch (err) {
        console.error(err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchInitialData();
  }, []);

  // Refresh orders
  const refreshOrders = async () => {
    try {
      const res = await fetch(API_ORDERS_URL);
      if (!res.ok) throw new Error('Failed to refresh orders');
      const data = await res.json();
      setOrders(data);
    } catch (err) {
      console.error(err);
    }
  };

  // Utility
  const calculateTotal = () =>
    cart.reduce((total, item) => total + item.unit_price * item.quantity, 0);

  // Add to cart
  const addToCart = (product, quantity) => {
    setCart((prevCart) => {
      const existing = prevCart.find((i) => i.product_id === product.id);
      if (existing) {
        return prevCart.map((i) =>
          i.product_id === product.id
            ? { ...i, quantity: i.quantity + quantity }
            : i
        );
      }
      return [
        ...prevCart,
        {
          product_name: product.name,
          product_id: product.id,
          unit_price: parseFloat(product.price),
          quantity,
        },
      ];
    });
    setOrderStatus(null);
  };

  // Update cart item
  const updateCartItem = (productId, quantity) => {
    if (quantity <= 0) {
      setCart(cart.filter((item) => item.product_id !== productId));
    } else {
      setCart(
        cart.map((item) =>
          item.product_id === productId ? { ...item, quantity } : item
        )
      );
    }
  };

  // Create order
  const createOrder = async () => {
    if (cart.length === 0) return alert('Cart is empty');

    const payload = {
      total_amount: calculateTotal(),
      status: 'created',
      items: cart,
    };

    try {
      const res = await fetch(API_ORDERS_URL, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });
      if (!res.ok) throw new Error('Failed to create order');
      const data = await res.json();

      const newOrder = {
        order_number: data.data.order_number,
        status: 'created',
        total_amount: payload.total_amount,
        items: cart,
      };

      setOrder(newOrder);
      setCart([]);
      await refreshOrders();
    } catch (err) {
      alert(`Error: ${err.message}`);
    }
    setOrderStatus('created'); // set order status
  };

  // Checkout order
  const checkout = async () => {
    if (!order) return alert('No order selected');

    const payload = {
      order_number: order.order_number,
      total_amount: order.total_amount,
      status: 'confirmed',
      items: order.items,
    };

    try {
      const res = await fetch(API_ORDERS_URL, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });
      if (!res.ok) throw new Error('Failed to confirm order');

      setOrder({ ...order, status: 'confirmed' });
      await refreshOrders();
    } catch (err) {
      alert(`Error: ${err.message}`);
    }
  };

  // Cancel order (full payload)
  const cancelOrderWithPayload = async (orderToCancel) => {
    if (!orderToCancel) return alert('No order selected');

    const payload = {
      order_number: orderToCancel.order_number,
      total_amount: orderToCancel.total_amount,
      status: 'cancelled',
      items: orderToCancel.items.map((item) => ({
        product_id: item.product_id,
        unit_price: item.unit_price,
        quantity: item.quantity,
      })),
    };

    try {
      const res = await fetch(API_ORDERS_URL, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });
      if (!res.ok) throw new Error('Failed to cancel order');

      if (order?.order_number === orderToCancel.order_number) {
        setOrder({ ...order, status: 'cancelled' });
      }

      await refreshOrders();
    } catch (err) {
      alert(`Error: ${err.message}`);
    }
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100">
        <div className="text-center">
          <div className="animate-spin rounded-full h-16 w-16 border-b-4 border-blue-500 mx-auto mb-6"></div>
          <p className="text-xl text-blue-700 font-semibold">Loading your store...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex justify-center items-center min-h-screen bg-red-50">
        <div className="text-center">
          <div className="text-red-500 mb-4">
            <svg className="w-16 h-16 mx-auto" fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7 4a1 1 0 11-2 0 1 1 0 012 0zm-1-9a1 1 0 00-1 1v4a1 1 0 102 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
            </svg>
          </div>
          <p className="text-lg text-red-600 bg-red-100 p-4 rounded-lg shadow-md">Error: {error}</p>
        </div>
      </div>
    );
  }

  return (
    <div className="container mx-auto p-6 space-y-10 bg-gray-50 min-h-screen">
      <h1 className="text-4xl font-bold text-center mb-8 text-indigo-800 bg-gradient-to-r from-indigo-600 to-purple-600 bg-clip-text text-transparent">
        E-Commerce Store
      </h1>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-10">
        <div className="bg-white rounded-xl shadow-lg p-6 border-l-4 border-blue-500">
          <h2 className="text-2xl font-semibold mb-6 text-blue-700">Products</h2>
          <ProductList products={products} addToCart={addToCart} />
        </div>

        <div className="space-y-6">
          <div className="bg-white rounded-xl shadow-lg p-6 border-l-4 border-purple-500">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-semibold text-purple-700">Cart</h2>
            </div>
            <Cart
              cart={cart}
              updateCartItem={updateCartItem}
              total={calculateTotal()}
              createOrder={createOrder}
              orderStatus={orderStatus}
              setOrderStatus={setOrderStatus} // ‚úÖ provide setter
              checkout={checkout}
              cancelOrder={cancelOrderWithPayload}
            />
          </div>

          {order && (
            <OrderSummary
              order={order}
              checkout={checkout}
              cancelOrder={cancelOrderWithPayload}
            />
          )}
        </div>
      </div>

      <div className="bg-white rounded-xl shadow-lg p-6 border-l-4 border-green-500">
        <h2 className="text-2xl font-semibold mb-6 text-green-700">All Orders</h2>
        <OrderList orders={orders} refreshOrders={refreshOrders} />
      </div>
    </div>
  );
}